/** @jsx React.DOM */
var React = require('react');

/* Views */
var QuerySaveModal = require('./QuerySaveModal.react');

/* Helpers */
var CodeMirror = require('codemirror'),
    OverlayMixin  = require('react-bootstrap').OverlayMixin;

require('codemirror/mode/sql/sql');

var QueryEditor = React.createClass({
  displayName: 'QueryEditor',
  mixins: [OverlayMixin],

  componentDidMount: function() {
    this.editor = CodeMirror.fromTextArea(this.refs.queryEditor.getDOMNode(), {
      lineNumbers: true,
      tabSize: 2,
      mode: 'text/x-mysql',
      theme: 'blackboard'
    });
  },

  getInitialState: function() {
    return { isModalOpen: false };
  },

  handleToggle: function () {
    this.setState({
      isModalOpen: !this.state.isModalOpen
    });
  },

  render: function () {
    return (
      React.createElement("div", {className: "row spaced"}, 
        React.createElement("div", {className: "col-sm-12"}, 

          React.createElement("div", {className: "panel panel-default"}, 
            React.createElement("div", {className: "panel-heading"}, 
              React.createElement("h3", {className: "panel-title"}, "Query editor")
            ), 
            React.createElement("div", {className: "panel-body"}, 
              React.createElement("div", {className: "col-sm-12"}, 
                React.createElement("textarea", {ref: "queryEditor", name: "query-editor"})
              )
            )
          )

        ), 

        React.createElement("div", {className: "col-sm-12"}, 
          React.createElement("div", {className: "row"}, 

            React.createElement("div", {className: "col-sm-6"}, 
              React.createElement("input", {ref: "customName", type: "text", name: "custom-name", className: "form-control", placeholder: "Select a custom table name"})
            ), 

            React.createElement("div", {className: "col-sm-6 text-right"}, 
              React.createElement("div", {className: "btn-toolbar pull-right"}, 
                React.createElement("div", {className: "btn-group"}, 
                  React.createElement("button", {className: "btn btn-primary", onClick: this.handleToggle}, "Save Query")
                ), 

                React.createElement("div", {className: "btn-group"}, 
                  React.createElement("button", {className: "btn btn-success"}, "Run Query")
                )
              )
            )

          )
        )
      )
    );
  },

  renderOverlay: function() {
    if( !this.state.isModalOpen ) return(React.createElement("span", null));

    // Render the modal when it's needed
    return (React.createElement(QuerySaveModal, {onRequestHide: this.handleToggle, query: this.editor.getValue()}));
  }
});

module.exports = QueryEditor;