/** @jsx React.DOM */
var React = require('react');

/* Components */
var SearchInputField = require('./SearchInputField.react');

/* Actions */
var TableActions = require('../actions/TableActions');

/* Header component */
var TableSearch = React.createClass({
  displayName: 'TableSearch',
  render: function () {
    return (
      React.createElement("section", {className: "row table-search-row"}, 
        React.createElement("div", {className: "col-sm-12"}, 
          React.createElement("div", {className: "row"}, 

            React.createElement("form", {className: "col-sm-7", role: "form"}, 
              React.createElement("div", {className: "form-group"}, 
                React.createElement("label", {htmlFor: "tables-input"}, "Tables:"), 
                React.createElement(SearchInputField, {placeholder: "Select a table", selectizeOptions: this.tableSelectizeOptions})
              )
            ), 

            React.createElement("form", {className: "col-sm-5", role: "form"}, 
              React.createElement("div", {className: "form-group"}, 
                React.createElement("label", {htmlFor: "tables-input"}, "Partition:"), 
                React.createElement(SearchInputField, {placeholder: "Select a partition", disabled: "true", selectizeOptions: this.partitionSelectizeOptions})
              )
            )

          )
        )
      )
    );
  },

  /* - Selectize options --------------------------------------------------- */
  tableSelectizeOptions: function() {
    return {
      preload: true,
      render: { option: this._renderTableOptions },
      valueField: 'fqn',
      labelField: 'fqn',
      searchField: ['fqn', 'tableName', 'schema'],
      plugins: {
        'remove_button': {},
        'header': {
          headers: ['Table', 'Usages', 'Last Updated']
        }
      },

      load: function(query, callback) {
        $.ajax({
          url: './api/table',
          type: 'GET',

          error: function() { callback(); },
          success: function(res) { callback(res); }
        });
      },

      onItemAdd: function(table, $element) {
        TableActions.addTable({ name: table });
      }.bind(this),

      onItemRemove: function(table) {
        TableActions.removeTable(table);
      }.bind(this),

      onItemSelected: function(element) {
        TableActions.selectTable($(element).data('value'));
      }.bind(this)
    };
  },

  _renderTableOptions: function(item, escape) {
    return (
      '<div class="row">' +
        '<div class="col-sm-6"><p>' + escape(item.fqn) + '</p></div>' +
        '<div class="col-sm-3"><p>' + escape(item.usages) + '</p></div>' +
      '</div>'
    );
  },

  partitionSelectizeOptions: function() {
    return {};
  }
});

module.exports = TableSearch;