/** @jsx React.DOM */
var React = require('react');

/* Stores */
var UserStore = require('../stores/UserStore');

/* Utils */
var UserApiUtils = require('../utils/UserApiUtils');

// State actions
function getStateFromStore() {
  return {
    user: UserStore.getCurrentUser()
  };
}

/* Header component */
var Header = React.createClass({
  displayName: 'Header',

  getInitialState: function() {
    return getStateFromStore();
  },

  componentWillMount: function() {
    UserApiUtils.getCurrentUser();
  },

  componentDidMount: function() {
    UserStore.addStoreListener('add', this._onChange);
  },

  componentWillUnmount: function() {
    UserStore.removeStoreListener('add');
  },

  render: function () {
    return (
      React.createElement("header", {className: "row header-row"}, 

        React.createElement("div", {className: "col-sm-7"}, 
          React.createElement("h1", null, "AirPal")
        ), 

        React.createElement("div", {className: "col-sm-5"}, 

          React.createElement("div", {className: "row user-info"}, 
            React.createElement("div", {className: "col-sm-4"}, 
              React.createElement("dl", null, 
                React.createElement("dt", null, "User Name"), 
                React.createElement("dd", {className: "user-name"}, this.state.user.name)
              )
            ), 

            React.createElement("div", {className: "col-sm-4"}, 
              React.createElement("p", null, 
                React.createElement("strong", null, "Access Level "), 

                React.createElement("a", {href: "https://airbnb.hackpad.com/Airpal-9FiIU3O2BJ1#:h=Access-Levels", target: "_blank", 
                  'data-toggle': "tooltip", 'data-placement': "bottom", title: "For more info about the access rights, see the HackPad"}, 
                  React.createElement("span", {className: "glyphicon glyphicon-info-sign"})
                ), 

                React.createElement("br", null), 

                React.createElement("span", {className: "user-permissions"}, 
                  this.state.user.executionPermissions.accessLevel
                )
              )
            ), 

            React.createElement("div", {className: "col-sm-4 text-right"}, 
              React.createElement("a", {href: "#", className: "btn btn-default", id: "start-tour"}, "Take Tour")
            )

          )

        )

      )
    );
  },

  /* Store events */
  _onChange: function() {
    this.setState(getStateFromStore());
  }
});

module.exports = Header;