/** @jsx React.DOM */
var React = require('react');

/* Actions */
var QueryActions = require('../actions/QueryActions');

/* Helpers */
var Modal = require('react-bootstrap').Modal;

/* Stores */
var QueryStore = require('../stores/QueryStore');

var QuerySaveModal = React.createClass({
  displayName: 'QuerySaveModal',

  componentDidMount: function() {
    QueryStore.addStoreListener('add', this._onChange);
  },

  componentWillUnmount: function() {
    QueryStore.removeStoreListener('add', this._onChange);
  },

  render: function () {
    return (
      React.createElement(Modal, React.__spread({},  this.props, {title: "Save a new query"}), 
        React.createElement("div", {className: "modal-body"}, 
          React.createElement("form", {className: "form-horizontal", action: "#", onSubmit: this.handleSaveRequest}, 

            React.createElement("pre", null, this.props.query), 

            React.createElement("div", {className: "form-group"}, 
              React.createElement("label", {className: "col-sm-4 text-right", htmlFor: "query-name"}, "Query Name"), 
              React.createElement("div", {className: "col-sm-8"}, 
                React.createElement("input", {ref: "name", name: "query-name", type: "text", className: "form-control"})
              )
            ), 

            React.createElement("div", {className: "form-group"}, 
              React.createElement("label", {className: "col-sm-4 text-right", htmlFor: "query-description"}, "Query Description"), 
              React.createElement("div", {className: "col-sm-8"}, 
                React.createElement("textarea", {ref: "description", rows: "4", name: "query-description", className: "form-control"})
              )
            )

          )
        ), 

        React.createElement("div", {className: "modal-footer"}, 
            React.createElement("div", {ref: "saveQueryIndicator", className: "hidden"}, 
              React.createElement("span", {className: "glyphicon glyphicon-repeat"}), " Saving query..."
            ), 

            React.createElement("button", {type: "button", className: "btn btn-default", onClick: this.props.onRequestHide}, "Cancel"), 
            React.createElement("button", {type: "button", className: "btn btn-primary", onClick: this.handleSaveRequest}, "Save Query")
        )
      )
    );
  },

  handleSaveRequest: function(event) {
    event.preventDefault();

    // Extract the data from the view and pass the data to the
    // view action to create a new query
    var name = this.refs['name'].getDOMNode().value,
        description = this.refs['description'].getDOMNode().value;
    if ( name == '' ) return;

    // Start the "saving process"
    $(this.refs['saveQueryIndicator'].getDOMNode()).removeClass('hidden');

    // Send the data await
    QueryActions.createQuery({
      query: this.props.query,
      name: name,
      description: description
    });
  },

  _onChange: function() {
    console.log('on change');
  }
});

module.exports = QuerySaveModal;